<template>
  <div class="container">
    <!-- name area -->
    <div class="name">
      <h3>Name：{{ userName }}</h3>
      <div class="reset" @click="setName()">Reset Name</div>
    </div>
    <!-- chat room -->
    <div class="chatRoom">
      <!-- head -->
      <div class="roomHead">
        <div class="roomHead__topButtons">
          <div class="roomHead__button close"></div>
          <div class="roomHead__button minimize"></div>
          <div class="roomHead__button zoom"></div>
        </div>
        <img src="https://lorempixel.com/50/50/" class="roomHead__img" draggable="false">
        <div class="roomHead__title">Test Room</div>
      </div>
      <!-- body -->
      <div id="js-roomBody" class="roomBody">
        <template v-for="item in messages">
          <!-- other people -->
          <template v-if="item.userName != userName">
            <div class="messageBox">
              <img src="https://lorempixel.com/40/40/" class="messageBox__img" draggable="false">
              <div class="messageBox_content">
                <div class="messageBox__name">{{item.userName}}</div>
                <div class="messageBox__text">{{item.message}}</div>
              </div>
              <div class="messageBox__time">{{item.timeStamp}}</div>
            </div>
          </template>
          <!-- self -->
          <template v-if="item.userName == userName">
            <div class="messageBox messageBox--self">
              <div class="messageBox__time messageBox__time--self">{{item.timeStamp}}</div>
              <div class="messageBox_content messageBox_content--self">
                <div class="messageBox__text messageBox__text--self">{{item.message}}</div>
              </div>
            </div>
          </template>
        </template>
      </div>
      <!-- bottom -->
      <div class="roomBottom" v-bind:class="{ disable: !userName }">
        <div class="roomBottom__tools"></div>
        <div class="roomBottom__input">
          <textarea id="js-message" class="roomBottom__input__textarea" rows="1" v-bind:class="{ disable: !userName }" @keydown.enter="sendMessage($event)"></textarea>
        </div>
      </div>
    </div>
    <!-- modal -->
    <div id="js-modal" class="modal">
      <div class="modal__container">
        <header class="modal__header">
          <h2 class="view-title">輸入名稱</h2>
        </header>
        <div class="modal__body">
          <input type="text" id="js-userName" class="userName" maxlength="6" @keydown.enter="saveName()">
          <div class="button" @click="saveName()">設定</div>
        </div>
        <footer class="modal__footer"></footer>
      </div>
    </div>
  </div>
</template>

<script>
const msgRef = firebase.database().ref('/messages/');
export default {
  name: 'ChatRoom',
  data() {
    return {
      userName: '',
      messages: []
    }
  },
  methods: {
    setName() {
      document.querySelector('#js-modal').style.display = 'block';
    },
    saveName() {
      const vm = this;
      const userName = document.querySelector('#js-userName').value;
      if (userName.trim() == '') { return; }
      vm.userName = userName;
      document.querySelector('#js-modal').style.display = 'none';
    },
    getTime() {
      const now = new Date();
      const hours = now.getHours();
      const minutes = now.getMinutes();
      const format = (hours >= 12) ? "下午" : "上午";
      return `${format} ${hours}:${minutes}`;
    },
    sendMessage(e) {
      const vm = this;
      let userName = document.querySelector('#js-userName');
      let message = document.querySelector('#js-message');
      if (e.shiftKey) {
        return false;
      }
      if(message.value.length <=1 && message.value.trim() == '') {
        message.value = '';
        e.preventDefault();
        return false;
      }
      msgRef.push({
        userName: userName.value,
        message: message.value,
        timeStamp: vm.getTime()
      })
      message.value = '';
      e.preventDefault();
    }
  },
  mounted() {
    const vm = this;
    msgRef.on('value', function(snapshot) {
      const val = snapshot.val();
      vm.messages = val;
    })
  },
  updated() {
    const roomBody = document.querySelector('#js-roomBody');
    roomBody.scrollTop = roomBody.scrollHeight;
  }
}
</script>

<style scoped>
* {
  font-family: '微軟正黑體';
  margin: auto;
}
.container {
  padding: 10px 50px 0px 0px;
  height: 900px;
}
.name {
  text-align: center;
  margin: 10px 50px 10px 0px;
  color: #333333;
}
.reset {
  margin-top: 10px;
  padding: 5px 10px;
  border-radius: 10px;
  font-weight: 600;
  color: #333333;
  background-color: #CCCCCC;
  display: inline-block;
  cursor: pointer;
}
.chatRoom {
  border-radius: 5px;
  max-width: 500px;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
}

/* Head */
.roomHead {
  width: 100%;
  height: 85px;
  border-radius: 5px 5px 0px 0px;
  background-color: #2B364B;
  position: relative;
}
.roomHead__topButtons {
  padding: 2px 0px 5px 10px;
  text-align: left;
}
.roomHead__button {
  height: 10px;
  width: 10px;
  border-radius: 50%;
  margin: 0px 1px;
  display: inline-block;
  cursor: pointer;
}
.close {
  background-color: #FF625A;
}
.minimize {
  background-color: #FFC02F;
}
.zoom {
  background-color: #28CB40;
}
.roomHead__img {
  width: 50px;
  height: 50px;
  margin: 0px 10px 6px 12px;
  border-radius: 50%;
  position: absolute;
  cursor: pointer;
}
.roomHead__title {
  font-size: 13px;
  font-weight: 600;
  color: #FFFFFF;
  height: 80px;
  margin: 5px 0px 0px 75px;
  position: absolute;
  cursor: pointer;
}

/* Body */
.roomBody {
  padding: 10px 0px;
  background-color: #fff;
  height: 60vh;
  overflow-y: auto;
  overflow-x: hidden;
}
.messageBox {
  padding: 5px 10px;
  position: relative;
}
.messageBox__img {
  height: 40px;
  width: 40px;
  border-radius: 50%;
  vertical-align: top;
  display: inline-block;
  cursor: pointer;
}
.messageBox_content {
  max-width: 70%;
  display: inline-block;
}
.messageBox__name {
  margin: 5px 0px 5px 5px;
  font-size: 13px;
  color: #727C8A;
  vertical-align: top;
  cursor: pointer;
}
.messageBox__text {
  margin: 5px 0px 5px 5px;
  padding: 8px 12px;
  font-size: 12px;
  letter-spacing: 0.6px;
  background-color: #E3E8EB;
  border-radius: 12px;
  line-height: 1.5;
  text-align: left;
  word-break: break-all;
  white-space: pre-line;
}
.messageBox__time {
  transform: scale(0.7);
  color: #ACB0B8;
  vertical-align: bottom;
  margin: 0px 0px 5px -12px;
  display: inline-block;
}
.messageBox--self {
  text-align: right;
}
.messageBox__text--self {
  background-color: #AFF47E;
  margin-right: 25px;
}
.messageBox__time--self {
  margin: 0px -16px 5px 0px;
}

/* Bottom */
.roomBottom {
  bottom: 0px;
  border-radius: 0px 0px 5px 5px;
  background-color: #FFFFFF;
}
.roomBottom__tools {
  border-top: solid 1px #E7E7E7;
  border-bottom: solid 2px #E7E7E7;
  background-color: #F6F6F6;
  height: 30px
}
.roomBottom__input {
  padding: 10px 10px 5px 10px;
}
.roomBottom__input__textarea {
  width: 100%;
  height: 60px;
  border: none;
  resize: none;
  outline: none;
}

/* status */
.disable {
  pointer-events: none;
  background-color: #ebebeb;
}

/* modal */
.modal {
  z-index: 3;
  padding: 50px 0px;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.4);
  animation: opac 0.8s;
  letter-spacing: 2px;
  text-align: unset;
}
.modal__container {
  margin: auto;
  position: relative;
  padding: 10px;
  outline: 0;
  max-width: 300px;
}
.modal__header {
  color: #fff;
  background-color: #2B364B;
  padding: 10px;
  text-align: center;
  border-radius: 5px 5px 0px 0px;
}
.modal__body {
  background-color: #fff;
  padding: 20px 50px;
  text-align: center;
}
.modal__body p {
  text-align: left;
  line-height: 24px;
}
.modal__img {
  max-width: 100%;
}
.modal__footer {
  color: #fff;
  background-color: #2B364B;
  height: 8px;
  border-radius: 0px 0px 5px 5px;
}

/* name set */
.userName {
  height: 30px;
  font-size: 16px;
  margin-bottom: 10px;
  border: solid 1px #cbcbcb;
  width: 70%;
  text-align: center;
  display: inline-block;
}
.button {
  font-size: 14px;
  color: #FFFFFF;
  background-color: #2B364B;
  padding: 10px 20px;
  display: inline-block;
  cursor: pointer;
}

/* media */
@media screen and (max-width: 425px) {
  .messageBox_content {
    max-width: 60%;
  }
}
@media screen and (max-width: 385px) {
  .messageBox_content {
    max-width: 50%;
  }
}
</style>



